import Vue from 'vue'
import Router from 'vue-router'
import index from '../../components/index/index/index.vue'
import type from '../../components/index/type/type.vue'
import typeSub from '../../components/index/typeSub/typeSub.vue'
import upload from '../../components/index/upload/upload.vue'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  base: '/',

  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },{
      path: '/type',
      name: 'type',
      component: type
    },{
      path: '/typeSub',
      name: 'typeSub',
      component: typeSub
    },{
      path: '/upload',
      name: 'upload',
      component: upload
    },
  ]
})
